.deco.deco_sh {
  box-shadow: 0px 4px 15px 2px rgba(160, 127, 136, 0.3);
}

.text__paragraph {
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
}

.relative-box__inner {
  height: 100%;
  position: relative;
  margin-bottom: 25px;
}

.relative-box__ration {
  padding-top: 56.25%;
  height: 0;
}

.relative-box__content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

/* Блоки Верстки */

.logo__inner {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo__img {
  max-width: 235px;
}

.logo_substate .logo__inner {
  background-color: #eedede;
  position: relative;
  z-index: 1;
  padding: 15px 30px;
}

/* Навигация */

* {
  box-sizing: border-box;
}

html {
  height: 100%;
  overflow-y: scroll;
}

body {
  height: 100%;
  -webkit-overflow-scrolling: touch;
}

@media screen and (min-width: 540px) {
  .footer__content {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
  }

  .footer__box {
    width: calc(50% - 30px);
  }

  .footer__box {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 540px) and (min-width: 768px) {
  .footer__content {
    margin-left: -10px;
    margin-right: -10px;
  }
}

@media screen and (min-width: 768px) {
  .layout__wrap {
    padding-left: 40px;
    padding-right: 40px;
  }

  .footer__box {
    margin-left: 10px;
    margin-right: 10px;
  }

  .footer__box {
    width: calc(25% - 20px);
  }

  .footer__row {
    margin-left: 10px;
    margin-right: 10px;
  }

  .button_font_mono .button__text {
    letter-spacing: 0.15rem;
  }

  .button_size_s .button__text {
    margin: 3px 30px;
  }

  .button_size_m {
    font-size: 16px;
    line-height: 22 px;
  }

  .logo_substate .logo__inner {
    padding: 40px 120px;
  }
}

@media screen and (min-width: 994px) {
  .header__inner {
    flex-direction: row;
    align-items: center;
  }

  .header__contact {
    width: 35%;
    margin-bottom: 0;
  }

  .header__logo {
    width: 30%;
    margin-bottom: 0;
  }

  .header__shop {
    width: 35%;
  }

  .button_size_s .button__text {
    margin: 5px 45px;
  }

  .button_size_m .button__text {
    margin: 7px 15px;
  }

  .button_size_l .button__text {
    margin: 10px 25px;
  }

  .button_size_xl .button__text {
    margin: 12px 90px;
  }

  .title__main {
    font-size: 30px;
    line-height: 37.5px;
  }

  .title__sub {
    font-size: 24px;
    line-height: 30px;
  }

  .title__mini {
    font-size: 18px;
    line-height: 22.5px;
  }

  .text__paragraph {
    margin-bottom: 25px;
  }
}

@media screen and (min-width: 1280px) {
  .layout__wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
