.deco.deco {
    &_bg_light {
        background-color: $color_light;
    }

    &_bg_active {
        background-color: $color_active;
    }

    &_text_active {
        color: $color_active;
    }

    &_text_light {
        color: $color_white;
    }

    &_more {
        font-size: 14px;
        display: inline-flex;
        position: relative;
        margin-left: -25px;
        &:after {
            content: "";
            position: absolute;
            right: -40px;
            top: 0;
            transform: translateY(50%);
            mask-image: url("../img/icon/icon_arrow_right.svg");
            mask-repeat: no-repeat;
            background-color: $color_white;
            width: 30px;
            height: 20px;
        }

        &_dark {
            color: $color_base;
            &:after {
                background-color: $color_base;
            }
        }
    }

    &_sh {
        box-shadow: 0px 4px 15px 2px rgba(160, 127, 136, 0.3);
    }
}
