$columns: 12;
$atom: (100% / $columns);

$break_bp360: 360px;
$break_bp375: 375px;
$break_bp540: 540px;
$break_bp768: 768px;
$break_bp994: 994px;
$break_bp1280: 1280px;
$break_bp1366: 1366px;
$break_bp1440: 1410px;

$offset: 30px;
$offset_bp360: $offset;
$offset_bp375: $offset;
$offset_bp540: $offset;
$offset_bp768: 20px;
$offset_bp994: $offset_bp768;
$offset_bp1280: $offset_bp768;
$offset_bp1366: $offset_bp768;
$offset_bp1440: $offset_bp768;

$offset_one_side: ($offset / 2);
$offset_bp360_one_side: ($offset_bp360 / 2);
$offset_bp375_one_side: ($offset_bp375 / 2);
$offset_bp540_one_side: ($offset_bp540 / 2);
$offset_bp768_one_side: ($offset_bp768 / 2);
$offset_bp994_one_side: ($offset_bp994 / 2);
$offset_bp1280_one_side: ($offset_bp1280 / 2);
$offset_bp1366_one_side: ($offset_bp1366 / 2);
$offset_bp1440_one_side: ($offset_bp1440 / 2);

$fields: 15px;
$fields_bp768: 40px;
$fields_bp1280: 15px;

@mixin wrapper-full() {
    padding-left: $fields;
    padding-right: $fields;

    @include bp768-block() {
        padding-left: $fields_bp768;
        padding-right: $fields_bp768;
    }

    @include bp1280-block() {
        padding-left: $fields_bp1280;
        padding-right: $fields_bp1280;
    }
}

@mixin row-offsets() {
    margin-left: ($offset_one_side * -1);
    margin-right: ($offset_one_side * -1);

    @include bp768-block() {
        margin-left: ($offset_bp768_one_side * -1);
        margin-right: ($offset_bp768_one_side * -1);
    }
}

@mixin col-offsets($type) {
    #{$type}-left: $offset_one_side;
    #{$type}-right: $offset_one_side;

    @include bp768-block() {
        #{$type}-left: $offset_bp768_one_side;
        #{$type}-right: $offset_bp768_one_side;
    }
}

@mixin wrapper() {
    max-width: 1410px;
    margin: 0 auto;
    @include wrapper-full();
}

@mixin row-flex() {
    display: flex;
    flex-wrap: wrap;
    @include row-offsets();
}

@mixin row-float() {
    @include row-offsets();
    @include clearfix();
}

@mixin row-ib() {
    @include row-offsets();
}

@mixin col() {
    box-sizing: border-box;
    word-wrap: break-word;
    @include col-offsets(margin);
}

@mixin col-float() {
    float: left;
}

@mixin col-ib() {
    display: inline-block;
    vertical-align: top;
}

@mixin col-padding() {
    word-wrap: break-word;
    @include col-offsets(padding);
}

@mixin size($n) {
    $val: 100% / $columns * $n;
    width: #{calc(#{$val} - #{$offset})};
}

@mixin size-bp360($n) {
    $val: 100% / $columns * $n;
    @include bp360(width, #{calc(#{$val} - #{$offset_bp360})});
}

@mixin size-bp375($n) {
