.relative-box {
    &__inner {
        height: 100%;
        position: relative;
        margin-bottom: 25px;
    }

    &__ration {
        padding-top: 56.25%;
        height: 0;
    }

    &__content {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: none;
    }
}
