.title {
    &__main {
        font-weight: 600;
        font-size: 26px;
        line-height: 34px;
        @include bp994-block() {
            font-size: 30px;
            line-height: 37.5px;
        }
    }

    &__sub {
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;

        @include bp994-block() {
            font-size: 24px;
            line-height: 30px;
        }
    }

    &__mini {
        font-weight: 600;
        font-size: 22px;
        line-height: 26px;

        @include bp994-block() {
            font-size: 18px;
            line-height: 22.5px;
        }
    }
}
