.header {
    &__line {
        display: flex;
        align-items: stretch;
    }

    &__menu {
        margin-top: 0;
        margin-bottom: 0;
        width: 100%;
        height: 100%;
        display: flex;
    }

    &__inner {
        padding-top: 25px;
        padding-bottom: 20px;
        display: flex;

        flex-direction: column;
        @include bp994-block() {
            flex-direction: row;
            align-items: center;
        }
    }

    /* &__menu-list {
        background-color: $color_light;
    } */

    &__contact {
        width: 100%;
        display: flex;
        margin-bottom: 40px;
        @include bp994-block() {
            width: 35%;
            margin-bottom: 0;
        }
    }

    /* &__callback {
        display: flex;
        justify-content: center;

        @include bp994-block() {
            justify-content: flex-end;
        }

        @include bp994-block() {
            justify-content: flex-end;
        }
    }
 */
    &__logo {
        width: 100%;
        max-height: 180px;
        display: flex;
        justify-content: center;
        margin-bottom: 40px;
        @include bp994-block() {
            width: 30%;
            margin-bottom: 0;
        }
    }

    &__shop {
        width: 100%;
        /*  display: flex;
        justify-content: center;
        align-items: center; */
        @include bp994-block() {
            width: 35%;
        }
    }
}
