.footer {
    &__inner {
        display: flex;
        flex-direction: column;
        position: relative;
        overflow: hidden;
        z-index: 1;

        &:before {
            position: absolute;
            z-index: 0;
            content: "";
            mask-image: url("../img/deco/deco_flower_2.svg");
            mask-repeat: no-repeat;
            background-color: $color_passive;
            width: 580px;
            height: 745px;
            transform: rotate(-30deg) scale(0.6);
            right: -260px;
            top: 0;

            /*  @include bp994-block() {
                width: 580px;
                height: 745px;
                right: 15px;
                top: -150px;
            } */
        }

        &:after {
            position: absolute;
            z-index: 0;
            content: "";
            mask-image: url("../img/deco/deco_flower_1.svg");
            mask-repeat: no-repeat;
            background-color: $color_passive;
            width: 580px;
            height: 745px;
            /* transform: rotate(0deg) scale(0.6); */

            left: -200px;
            bottom: -470px;

            /*  @include bp994-block() {
                width: 580px;
                height: 745px;
                left: 15px;
            } */
        }
    }

    &__line-box {
        position: relative;
        z-index: 1;
        width: 100%;
        position: relative;

        &:after {
            position: absolute;
            content: "";
            width: 100%;
            height: 2px;
            top: 50%;
            background-color: $color_active;
        }
    }

    &__logo {
        position: relative;
        z-index: 1;
        display: flex;
        justify-content: center;
    }

    &__content-box {
        position: relative;
        width: 100%;
    }

    &__content {
        /* display: flex;
        justify-content: center; */
        position: relative;
        z-index: 1;
        @include bp540-block() {
            @include row-flex();
        }
    }

    &__box {
        @include col();
        @include size(12);
        @include size-bp540(6);
        @include size-bp768(3);

        position: relative;
        z-index: 1;
        display: flex;

        justify-content: center;

        margin-bottom: 20px;
        text-align: center;
        @include bp540-block() {
            margin-bottom: 0;
        }
    }

    &__row {
        @include col();
        @include size(12);
        margin: 30px 0;
        display: flex;
        justify-content: center;
    }

    &__media {
        position: relative;
        z-index: 1;
        justify-content: center;
    }
}
